.main-page {
  padding-left: 7.2%;
  padding-right: 7.2%;
  padding-bottom: 30px;
  background-color: #ffffff;
  color: #000000;
}

.main-page__image-wrapper a {
  display: block;
}

.main-page__image-wrapper img {
  display: block;
  margin: 0 auto;
}

.main-page__count-information {
  margin: 0;
  margin-top: 20px;
  text-align: center;
}

.main-page__books-list {
  display: flex;
  max-width: 1170px;
  margin: 0 auto;
  margin-top: 50px;
}

@media (max-width: 1050px) {
  .main-page__books-list {
    flex-wrap: wrap;
  }

  .main-page {
    padding-left: 2%;
    padding-right: 2%;
  }
}

@media (max-width: $mobile-width-only) {
  .main-page {
    padding-left: 4.7%;
    padding-right: 4.7%;
  }
}
